import React from "react";
import Hero from "./hero";
import HomeSearch from "./search";
import HomeClinic from "./clinic";
import Contact from "./contact";

const Home = (props) => {
  return (
    <div className="home-content" id="middle-home">
      <Hero />
      <Contact />
      <HomeSearch isHidden={true} />
      <HomeClinic isHidden={true} />
    </div>
  );
};
export default Home;
