import { faSearch } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import { Link, useNavigate } from "react-router-dom";

function HomeSearch(props) {
  const navigate = useNavigate();
  const [t, i18n] = useTranslation();

  const [state, setState] = useState({
    doctorLocation: "",
    doctorName: "",
  });

  // const [doctorLocation, setDoctorLocation] = useState("");
  // const [doctorName, setDoctorName] = useState("");

  const handleChange = (event) => {
    let value = event.target.value;
    let name = event.target.name;

    console.log(`[name]: ${typeof [name]} \n name: ${typeof name}`);
    console.log(`[value]: ${typeof [value]} \n value: ${typeof value}`);

    setState({
      ...state,
      [name]: value,
    });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    navigate("/" + i18n.language + "/treatments", { ...state });
  };
  return (
    <section className="section section-search" hidden={props.isHidden}>
      <div className="container-fluid">
        <div className="container banner-wrapper">
          <div className="justify-content-center banner-header text-center">
            <h1>{t("Home.slogan")}</h1>
            <p>{t("Home.sub_slogan")}</p>
          </div>
          <div
            className="row justify-content-center search-box"
            hidden={props.isHidden}
          >
            <form onSubmit={handleSubmit}>
              {/* <div className="form-group search-location mt-5">
                <input
                  name="doctorLocation"
                  value={state.doctorLocation}
                  onChange={handleChange}
                  type="text"
                  className="form-control"
                  placeholder="Search Location"
                />
                <span className="form-text">Based on your Location</span>
              </div> */}
              {/* <div className="form-group search-info mt-5">
                <input
                  name="doctorName"
                  value={state.doctorName}
                  onChange={handleChange}
                  type="text"
                  className="form-control"
                  placeholder="Search Doctors, Sepecialities Etc"
                /> */}
              {/* <span className="form-text">
                  Ex : Dental or Sugar Check up etc
                </span> */}
              {/* </div> */}
              {/* <button type="submit" className="btn btn-primary search-btn mt-5"> */}
              {/* <FontAwesomeIcon icon={faSearch} />  */}
              <button type="submit" className="btn btn-primary mt-3">
                <span>{t("Home.see_treatments")}</span>
              </button>
            </form>
          </div>
          <div className="justify-content-center banner-header text-center">
            <a
              href="https://forms.zohopublic.com/Medical-Portal/form/ServiceOrderForm/formperma/p2evCkxsmL2PtNfhs2EwXqAyhQhmW9IEeV3SaScjGFo"
              target={"_blank"}
              className="btn btn-primary mt-3"
            ></a>
          </div>
        </div>
      </div>
    </section>
  );
}

export default HomeSearch;
