import React, { useEffect, useState, useContext } from "react";
import { useTranslation } from "react-i18next";
import { Link, useLocation } from "react-router-dom";
import SpecialityService from "../../../services/speciality.service";

import { Swiper, SwiperSlide } from "swiper/react";
import { Keyboard, Mousewheel } from "swiper";
import AppContext from "store/app-store";

import "swiper/css";

const swiperConfigs = (isRtl) => ({
  dir: isRtl ? "rtl" : "ltr",
  direction: "horizontal",
  loop: true,
  slidesPerView: 1,
  spaceBetween: 30,
  mousewheel: {
    forceToAxis: true,
  },
  keyboard: {
    enabled: true,
    onlyInViewport: true,
  },
  modules: [Keyboard, Mousewheel],
  className: "mySwiper",
});

const HomeClinic = (props) => {
  const { getLang } = useContext(AppContext);
  const [t, i18n] = useTranslation();
  const location = useLocation();
  const [specilitiesData, setSpecilitiesData] = useState([]);

  const isRtl = () => getLang() === "ar";

  useEffect(() => {
    const getSpecialities = async () => {
      const specialityResponse = await SpecialityService.getSpecialities();
      setSpecilitiesData(specialityResponse.body);
    };

    if (!props.isHidden) {
      getSpecialities();
    }
  }, []);

  let specilitiesDataList =
    specilitiesData && specilitiesData[0] ? (
      specilitiesData
        // .filter((item) => item.active)
        .map((speciality, i) => (
          <SwiperSlide key={i}>
            <Link
              to="/treatments"
              state={{ ...speciality, prevPath: location.pathname }}
            >
              <div className="speicality-item text-center">
                <div className="speicality-img">
                  <img
                    src={speciality.image}
                    className="img-fluid"
                    alt="Speciality"
                  />
                  <span>
                    <i className="fa fa-circle" aria-hidden="true"></i>
                  </span>
                </div>
                <p>{speciality.name}</p>
              </div>
            </Link>
          </SwiperSlide>
        ))
    ) : (
      <></>
    );
  return (
    <section className="section section-specialities" hidden={props.isHidden}>
      <div className="container-fluid">
        <div className="section-header text-center">
          <h2>{t("Specialities")}</h2>
          <p className="sub-title">
            Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
            eiusmod tempor incididunt ut labore et dolore magna aliqua.
          </p>
        </div>
        <div className="row justify-content-center">
          <div className="col-md-6">
            <div className="specialities-slider slider">
              <Swiper {...swiperConfigs(isRtl())}>{specilitiesDataList}</Swiper>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default HomeClinic;
