// hero/index.jsx
import React, { useContext } from "react";
import HeroData from "./HeroData";
import { useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import styles from "./Hero.module.css";
import AppContext from "store/app-store";

const Hero = () => {
  const { getLang } = useContext(AppContext);
  const { t } = useTranslation();
  const navigate = useNavigate();

  // Select the first item from HeroData
  const heroItem = HeroData[0];

  return (
    <div
      className={`${styles.heroContainer} ${
        getLang() === "ar" ? styles.mirroredBackground : ""
      }`}
    >
      {/* Background image overlay */}
      <div className={styles.overlay}></div>

      {/* Hero content */}
      <div className={styles.heroContent}>
        <h1 className={styles.header}>
          {getLang() === "ar"
            ? "بوابتك الطبية"
            : "Your Gateway to Quality Medical Care"}
        </h1>
        <p className={styles.subHeader}>
          {getLang() === "ar"
            ? "احصل على أفضل خدمات الرعاية الصحية بمساعدة خبرائنا في بوابتك الطبية."
            : "Access world-class healthcare services with the help of YMP experts."}
        </p>
        <div className={styles.footer}>
          <button
            type="button"
            className={`btn btn-primary ${styles.heroButton}`}
            onClick={() =>
              window.open(
                "https://wa.me/962796998338?text=Hello%2C%20I%20would%20like%20to%20learn%20more%20about%20your%20medical%20services.",
                "_blank"
              )
            }
          >
            {getLang() === "ar" ? "تواصل معنا الآن" : "Contact Us Today"}
          </button>

          <button
            type="button"
            className={`btn btn-outline-secondary ${styles.heroButtonSecondary}`}
            onClick={() => navigate(`/${getLang()}/about-us`)}
          >
            {getLang() === "ar" ? "تعرف على خدماتنا" : "Learn More"}
          </button>
        </div>
      </div>
    </div>
  );
};

export default Hero;
