import ContactService from "client/services/contact.service";
import { Error } from "common/components";
import { useContext, useState } from "react";
import { Field, Form } from "react-final-form";
import { useTranslation } from "react-i18next";
import PhoneInput from "react-phone-number-input";
import "react-phone-number-input/style.css";
import { useNavigate } from "react-router-dom";
import styles from "./Contact.module.css";
import AppContext from "store/app-store";

const Contact = () => {
  const { getLang } = useContext(AppContext);
  const { t } = useTranslation();
  const [message, setMessage] = useState("");
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);

  const onSubmit = async (values) => {
    try {
      console.log("Calling ContactService.submit...");
      console.log(`values: ${values}`);
      const response = await ContactService.submit(values);
      console.log("Response:", response);
      if (response.status === 200) {
        // Navigate to the thank-you page only if the submission is successful
        navigate(`/${getLang()}/thank-you`);
      } else {
        throw new Error("Submission failed");
      }
    } catch (error) {
      // Log the error or display a user-friendly error message
      console.error("Submission Error:", error);
      setMessage("There was an issue with your submission. Please try again.");
    }
  };

  const validate = (values) => {
    const errors = {};
    if (!values.name) {
      errors.name = t("validation.required", { field: t("form.name") });
    } else if (values.name.length < 2) {
      errors.name = t("validation.tooShort", { field: t("form.name"), min: 2 });
    }
    if (!values.email) {
      errors.email = t("validation.required", { field: t("form.email") });
    } else if (
      !/^[\w-]+(\.[\w-]+)*@[a-zA-Z0-9-]+(\.[a-zA-Z0-9-]+)*(\.[a-zA-Z]{2,})$/.test(
        values.email.toLowerCase()
      )
    ) {
      errors.email = t("validation.invalidEmail");
    }
    if (!values.phone) {
      errors.phone = t("validation.required", { field: t("form.phone") });
    } else if (!/^\+?[1-9]\d{1,14}$/.test(values.phone)) {
      errors.phone = t("validation.invalidPhone");
    }
    if (values.message && values.message.length > 500) {
      errors.message = t("validation.tooLong", {
        field: t("form.message"),
        max: 500,
      });
    }
    return errors;
  };

  const PhoneAdapter = ({ input, placeholder }) => {
    const handlePhoneChange = (value) => {
      if (value && value.startsWith("+00")) {
        value = `+${value.slice(3)}`;
      }
      input.onChange(value);
    };

    return (
      <PhoneInput
        {...input}
        Style="border:0px;"
        international
        value={input.value}
        onChange={handlePhoneChange}
        placeholder={placeholder}
        className="form-control"
      />
    );
  };

  return (
    <section className={styles.contactSection}>
      <div className={styles.contactContent}>
        <h2>{t("form.aboutYMP")}</h2>
        <p>{t("form.aboutDescription")}</p>
        <button
          className={`btn btn-primary`}
          onClick={() => navigate(`/${getLang()}/what-to-expect`)}
        >
          {t("form.learnMore")}
        </button>
      </div>

      <div className={styles.formContainer}>
        <h3>{t("form.requestQuote")}</h3>
        <Form
          onSubmit={onSubmit}
          validate={validate}
          render={({ handleSubmit }) => (
            <form onSubmit={handleSubmit} className={styles.callbackForm}>
              <div className={styles.formGroup}>
                <Field
                  name="name"
                  component="input"
                  placeholder={t("form.name")}
                  className={styles.inputField}
                />
                <Error name="name" />
              </div>
              <div className={styles.formGroup}>
                <Field
                  name="phone"
                  placeholder={t("form.phone")}
                  component={PhoneAdapter}
                />
                <Error name="phone" />
              </div>
              <div className={styles.formGroup}>
                <Field
                  name="email"
                  component="input"
                  type="email"
                  placeholder={t("form.email")}
                  className={styles.inputField}
                />
                <Error name="email" />
              </div>
              <div className={styles.checkboxGroup}>
                <label>
                  <Field
                    name="services"
                    component="input"
                    type="checkbox"
                    value="Dental Care"
                  />
                  {t("form.dentalCare")}
                </label>
                <label>
                  <Field
                    name="services"
                    component="input"
                    type="checkbox"
                    value="IVF"
                  />{" "}
                  {t("form.ivf")}
                </label>
                <label>
                  <Field
                    name="services"
                    component="input"
                    type="checkbox"
                    value="Organ Transplant"
                  />{" "}
                  {t("form.organTransplant")}
                </label>
                <label>
                  <Field
                    name="services"
                    component="input"
                    type="checkbox"
                    value="Eye Care"
                  />{" "}
                  {t("form.eyeCare")}
                </label>
                <label>
                  <Field
                    name="services"
                    component="input"
                    type="checkbox"
                    value="Hair Transplant"
                  />{" "}
                  {t("form.hairTransplant")}
                </label>
                <label>
                  <Field
                    name="services"
                    component="input"
                    type="checkbox"
                    value="Other"
                  />{" "}
                  {t("form.other")}
                </label>
              </div>
              <div className={styles.formGroup}>
                <Field
                  name="message"
                  component="textarea"
                  placeholder={t("form.messagePlaceholder")}
                  rows="4"
                  className={styles.textArea}
                />
                <Error name="message" />
              </div>
              {message && <div className="error-message">{message}</div>}
              <button
                type="submit"
                className={`btn-primary ${styles.submitButton}`}
                disabled={loading}
              >
                {loading ? (
                  <span className="spinner-border spinner-border-sm"></span>
                ) : (
                  t("form.sendNow")
                )}
              </button>
            </form>
          )}
        />
      </div>
    </section>
  );
};

export default Contact;
